<template>
    <div id="my">
      <div class="bg" style=""></div>
      <div class="header">
        <!--<img src="../../static/img/nav_bg@3x.png"/>-->
        <div class="user-info">
          <div class="avatar">
            <!-- <img src="../../static/img/img_avatar@2x.png" @click="message"/> -->
						<img :src="imgd" @click="upimg"/>
						<!-- <van-cell value="" icon="shop-o" is-link v-on:click="_head"> -->
          </div>
          <div class="detail">
            <span>{{ messages }}（UID：{{UID}}）</span>
            <p>账户余额： {{balance}}</p>
          </div>
          <div class="user-level">
            等级：<span>{{classs}}</span>
          </div>
        </div>
      </div>
      <van-row class="center">
        <van-col span="8" style="padding-left: 40px;">
          <span>{{lastMonth}}</span>
          <p>上月结算</p>
        </van-col>
        <van-col span="8" style="border-left:1px solid #ededed;margin-left:60px;padding-left:30px;">
          <span style="margin-left: 28px;">{{estimate}}</span>
          <p style="width: 8rem;">本月预计收入</p>
        </van-col>
       <!-- <van-col span="8">
          <span>{{zongs}}</span>
          <p>总收入</p>
        </van-col> -->
      </van-row>
      <van-cell-group>
        <van-cell value="" is-link to="order">
          <template slot="title">
            <img src="../../static/img/icon_xld@3x.png" class="ico" />
            <span class="van-cell-text">我的订单</span>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <!-- <van-cell value="" is-link>
          <template slot="title">
            <img src="../../static/img/icon_cz@3x.png" class="ico" />
            <span class="van-cell-text">我的订单</span>
          </template>
        </van-cell> -->
				<van-cell value="" is-link to="upname">
				  <template slot="title">
				    <img src="../../static/img/icon_cwdl.png" class="ico" />
				    <span class="van-cell-text">修改昵称</span>
				  </template>
				</van-cell>
        <van-cell value="" is-link to="collection">
          <template slot="title">
            <img src="../../static/img/icon_scj@3x.png" class="ico" />
            <span class="van-cell-text">我的收藏</span>
          </template>
        </van-cell>
        <van-cell value="" is-link to="myroot">
          <template slot="title">
            <img src="../../static/img/qishou.png" class="ico" />
            <span class="van-cell-text">我的足迹</span>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell value="" is-link to="purse">
          <template slot="title">
            <img src="../../static/img/icon_cz@3x.png" class="ico" />
            <span class="van-cell-text">收入提取</span>
          </template>
        </van-cell>
				 <van-cell value="" is-link to="myinvate">
				  <template slot="title">
				    <img src="../../static/img/icon_yj@3x.png" class="ico" />
				    <span class="van-cell-text">我的邀请</span>
				  </template>
				</van-cell>
         <van-cell value="" is-link to="service">
          <template slot="title">
            <img src="../../static/img/img_kf.png" class="ico" />
            <span class="van-cell-text">我的客服</span>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
       <!--  <van-cell value="" is-link>
          <template slot="title">
            <img src="../../static/img/icon_cz@3x.png" class="ico" />
            <span class="van-cell-text">我的订单</span>
          </template>
        </van-cell> -->

        <!-- <van-cell value="" is-link to="">
          <template slot="title">
            <img src="../../static/img/icon_scj@3x.png" class="ico" />
            <span class="van-cell-text">使用帮助</span>
          </template>
        </van-cell> -->
        <van-cell value="" is-link to="aboutUs">
          <template slot="title">
            <img src="../../static/img/icon_wdxj.png" class="ico" />
            <span class="van-cell-text">关于我们</span>
          </template>
        </van-cell>
      </van-cell-group>
      <van-button type="danger" class="sub" @click="clear">退出登录</van-button>
    </div>
</template>

<script>
    export default {
        name: "my",
        data () {
          return {
            userInfo: '',
						imgd:'',
            messages: '',
            balance: '',
            lastMonth: '',
            estimate: '',
            UID: '',
            classs: '',
            zongs: '',
          }
        },
      created (){
        this.UserMessage()
      },
      methods: {
        UserMessage(){
          this.$POST('/user/user-info').then(res =>{
            console.log(res);
            if (res.code === 200) {
							this.imgd = res.data.avatar
              this.messages = res.data.nickname
              this.balance = res.data.credit4
              this.zongs = res.data.credit5
              this.lastMonth = res.data.upclose
              this.estimate = res.data.thisestimate
              this.UID = res.data.userid
              this.classs = res.data.lv
              if(this.classs == 1 ){
                this.classs = '合伙人'
              }else if( this.classs == 2 ){
                this.classs = '团长'
              }
            } else {
              this.$toast(res.msg)
            }
          }).catch(res => {
            this.$toast('连接服务器失败!');
            console.log(res);
          })
        },
        clear(){
          // this.$toast('注销成功');
          localStorage.removeItem('tokens')
          this.$router.push('/login');
        },

				upimg(){
					bridge.call("jsTakeUpload",localStorage.getItem("tokens"));
					bridge.register("addImgCallback",function(data){
						this.imgd = data;
						let param = {
						  avatar:data,
						}
						this.$POST('/user/updateavatar',param,true).then(res =>{
						  console.log(res)
						  if(res.code==200){
                this.$toast('修改成功!');
                UserMessage();
						  }
						}).catch(err=>{
						  console.log(err)
						})
					})
				}
      }
    }
</script>

<style scoped lang="less">
  .header {
    background: linear-gradient(to bottom right, #5abdff , #5a9bff);
    color: #fff;
    p {
      margin: 0;
      margin-top: 1.3rem;
    }
    .user-info {
      padding: 2.5rem .75rem .75rem .75rem;
      display: flex;
      text-align: left;
      justify-content: space-between;
      .avatar {
        width: 3.56rem;
        height: 3.56rem;
        line-height: 0;
        font-size: 0;
        margin-right: .8rem;
        img {
          width: 100%;
          height: 100%;
					border-radius: 50%
        }
      }
      .detail {
        font-size: .94rem;
        justify-content: space-between;
      }
      .user-level {
        font-size: .8rem;
        span {
          display: inline-block;
          padding: 0.4rem;
          background-color: #ed9700;
          border-radius: 0.53rem;
          font-size: .7rem;
        }
      }
    }
  }

  .center{
    background: linear-gradient(to bottom right, #6ba1ff , #6b93ff);
    padding:1rem 0;
    color: #ededed;
    border-top:1px solid #6b93ff;
    p{
      margin:0;
      margin-top:0.3rem;
    }
  }

  .van-cell-group {
    margin-bottom: .5rem;
  }
  .ico{
    float: left;
    display: block;
    width: 30px;
    height: 30px;
    margin-top: -3px;
  }
  #my .van-cell__title{
    flex:0.8;
    position: relative;
  }
  #my .van-cell__title>span{
    line-height: 28px;
    font-size: 17px;
    margin-left: 10px;
  }
  #my .van-cell{
    text-align: left;
   }
  .sub{
    width: 90%;
    border-radius: 25px;
    font-size: 18px;
    margin-top:25px;
  }
  .bg{
    position: absolute;
    width: 100%;
    height: 100%;
    background:#eeeeee;
    z-index: -2
  }
</style>



